  .user_unregistered-signup:hover {
    color: rgba(34, 34, 34, 0.85);
    /* background-color: #ccc; */
    background-color: #a749ff;
  }
  
  /**
   * * Login and signup forms
   * */
  .user_options-forms {
    position: absolute;
    top: 50%;
    left: 30px;
    width: calc(50% - 30px);
    min-height: 420px;
    background-color: #fff;
